.cc-cursor {
    position: fixed;
    top: 0;
    left: 0;
    width: 28px;
    height: 28px;
    pointer-events: none;
    user-select: none;
    will-change: transform, opacity;
    transform: translate3d(-100px, -100px, 0);
    z-index: 9999;
    opacity: 0;
    transition: opacity 180ms ease;
}

.cc-cursor[data-visible="1"] { opacity: 1; }

/* Helmet sticks to the pointer (scaled up slightly on hover) */
.cc-cursor-helmet {
    width: 34px;
    height: 34px;
    transform-origin: 50% 50%;
    transition: opacity 180ms ease, filter 180ms ease;
    filter: drop-shadow(0 2px 4px rgba(0,0,0,0.2));
    z-index: 10000; /* keep above rocket */
    --cc-scale: 1;
}
.cc-cursor-helmet[data-hover="0"] { opacity: 0; }
.cc-cursor-helmet[data-hover="1"] {
    /* Drive scale via CSS variable, composed into inline transform */
    --cc-scale: 1.12;
    filter: drop-shadow(0 4px 10px rgba(0,0,0,0.25));
}

/* Rocket trails behind and rotates with velocity */
.cc-cursor-rocket {
    width: 40px;
    height: 40px;
    transform-origin: 50% 50%;
    mix-blend-mode: normal;
    opacity: 0.9;
    transition: opacity 220ms ease;
}
.cc-cursor-rocket[data-hover="1"] { opacity: 0; }

/* Hide native cursor only when the feature is active */
body.custom-cursor-enabled {
    cursor: none !important;
}
/* Hide native cursor for all descendants by default */
body.custom-cursor-enabled * {
    cursor: none !important;
}
/* But keep text cursor for text-editable fields for usability */
body.custom-cursor-enabled input:focus,
body.custom-cursor-enabled textarea:focus,
body.custom-cursor-enabled [contenteditable]:focus,
body.custom-cursor-enabled [contenteditable="true"]:focus {
    cursor: text !important;
}


