.syllabus-list-container {
	padding: 24px;
}

.syllabus-list-header {
	margin-bottom: 24px;
}

.syllabus-list-header h2 {
	margin: 0;
	color: #1890ff;
	font-size: 24px;
	font-weight: 600;
	display: flex;
	align-items: center;
}

.chapter-list-header {
	margin-bottom: 24px;
}

/* Chapter title theme styles */
.chapter-title.light-theme {
	color: black !important;
}

.chapter-title.dark-theme {
	color: white !important;
}

.chapter-list-header h3 {
	margin: 0;
	color: #52c41a;
	font-size: 20px;
	font-weight: 600;
	display: flex;
	align-items: center;
}

.lesson-list-header {
	margin-bottom: 24px;
}

.lesson-list-header h3 {
	margin: 0;
	color: #fa8c16;
	font-size: 18px;
	font-weight: 600;
	display: flex;
	align-items: center;
}

.syllabus-list-container .ant-card {
	border-radius: 8px;
	box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
	background-color: #E6F5FF;
}

/* Main container card */
.syllabus-list-container .main-container-card {
	background-color: #E6F5FF;
	border: none;
	box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

/* Space theme main container card */
.space-theme .syllabus-list-container .main-container-card {
	background: rgba(110, 92, 172, 0.4) !important;
	backdrop-filter: blur(1px);
	border: 1px solid rgba(77, 208, 255, 0.3);
	box-shadow: 0 8px 32px rgba(77, 208, 255, 0.15);
}

/* Sun theme main container card */
.sun-theme .syllabus-list-container .main-container-card {
	background: rgba(255, 255, 255, 0.4) !important;
	backdrop-filter: blur(1px);
	border: 1px solid rgba(0, 0, 0, 0.1);
	box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
}

/* Inner table card */
.syllabus-list-container .table-card {
	background-color: #ffffff;
	border: none;
	box-shadow: 0 1px 4px rgba(0, 0, 0, 0.1);
}

/* Space theme inner table card */
.space-theme .syllabus-list-container .table-card {
	background: rgba(26, 26, 46, 0.7) !important;
	backdrop-filter: blur(1px);
	border: 1px solid rgba(77, 208, 255, 0.3);
	box-shadow: 0 8px 32px rgba(77, 208, 255, 0.15);
}

/* Sun theme inner table card */
.sun-theme .syllabus-list-container .table-card {
	background: rgba(255, 255, 255, 0.4) !important;
	backdrop-filter: blur(1px);
	border: 1px solid rgba(0, 0, 0, 0.1);
	box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
}


.syllabus-list-container .ant-btn {
	border-radius: 6px;
	font-weight: 500;
}

/* Refresh button styling to match level page */
.syllabus-list-container .refresh-button {
	height: 40px;
	border-radius: 8px;
	font-weight: 500;
	font-size: 16px;
	border: 1px solid rgba(77, 208, 255, 0.3);
	background: linear-gradient(135deg, #FFFFFF 0%, #9F96B6 46%, #A79EBB 64%, #ACA5C0 75%, #6D5F8F 100%);
	color: #000;
	backdrop-filter: blur(10px);
	transition: all 0.3s ease;
}

.syllabus-list-container .refresh-button:hover {
	background: linear-gradient(135deg, #FFFFFF, #B6D8FE 77%, #94C2F5);
	border-color: rgba(77, 208, 255, 0.6);
	transform: translateY(-2px);
	box-shadow: 0 4px 12px rgba(77, 208, 255, 0.3);
}

/* Sun theme refresh button */
.syllabus-list-container .sun-refresh-button {
	height: 40px;
	border-radius: 8px;
	font-weight: 500;
	font-size: 16px;
	border: 1px solid rgba(77, 208, 255, 0.3);
