	color: #000;
	backdrop-filter: blur(10px);
	transition: all 0.3s ease;
}

.syllabus-list-container .sun-refresh-button:hover {
	background: linear-gradient(135deg, #E0E0E0, #A0C0E0 77%, #80A0D0);
	border-color: rgba(77, 208, 255, 0.6);
	transform: translateY(-2px);
	box-shadow: 0 4px 12px rgba(77, 208, 255, 0.3);
}

/* Space theme refresh button */
.syllabus-list-container .space-refresh-button {
	height: 40px;
	border-radius: 8px;
	font-weight: 500;
	font-size: 16px;
	border: 1px solid rgba(77, 208, 255, 0.3);
	background: linear-gradient(135deg, #FFFFFF 0%, #9F96B6 46%, #A79EBB 64%, #ACA5C0 75%, #6D5F8F 100%);
