.sun-theme .custom-dropdown .ant-select-selection-item {
	color: #000 !important;
}

.sun-theme .custom-dropdown .ant-select-selection-placeholder {
	color: rgba(0, 0, 0, 0.6) !important;
}

.sun-theme .custom-dropdown .ant-select-arrow {
	color: rgba(0, 0, 0, 0.6) !important;
}

.sun-theme .custom-dropdown:hover .ant-select-selector {
	border-color: rgba(0, 0, 0, 0.3) !important;
}

.sun-theme .custom-dropdown:hover .ant-select-selector::before {
	border-color: rgba(0, 0, 0, 0.4) !important;
}

.sun-theme .custom-dropdown.ant-select-focused .ant-select-selector {
	border-color: rgba(0, 0, 0, 0.3) !important;
	box-shadow: 0 0 0 2px rgba(0, 0, 0, 0.1) !important;
}

.sun-theme .custom-dropdown.ant-select-focused .ant-select-selector::before {
	border-color: rgba(0, 0, 0, 0.4) !important;
}

